.ChevronIcon {
    fill: var(--color-black);
    cursor: pointer;

    &_direction {
        &_left {
            transform: rotate(180deg);

            [dir="rtl"] & {
                transform: none;
            }
        }

        &_right {
            [dir="rtl"] & {
                transform: rotate(180deg);
            }
        }

        &_bottom {
            transform: rotate(90deg);
        }

        &_top {
            transform: rotate(-90deg);
        }
    }
}
