.ResetAttributes {
    @include desktop {
        margin-block-end: 20px;
    }

    @include mobile {
        margin: 16px;
        margin-block-end: 0;
    }

    &-AttributeValue {
        margin: 10px 0;
        display: flex;
        align-items: center;
    }

    &-AttributeText {
        inset-inline-start: 10px;
        font-size: 14px;
    }

    &-AttributeLabel {
        font-weight: bold;
    }

    &-AttributeOption {
        img {
            height: 32px;
            object-fit: cover;
            vertical-align: middle;
            width: 32px;
        }

        .Image-Content {
            position: initial;
        }
    }

    &-CloseIcon {
        .CloseIcon {
            height: var(--checkbox-height);
            width: var(--checkbox-width);
        }
    }

    &-Title {
        @include mobile {
            display: none;
        }

        @include desktop {
            margin-block-start: 0;
            text-transform: none;
        }
    }

    &-MobileTitle {
        @include mobile {
            padding-inline-start: 16px;
            font-weight: 700;
            padding-block-start: 20px;
        }

        @include desktop {
            display: none;
        }
    }
}
