.Breadcrumb {
    $crumb-padding: 12px;

    display: inline-block;
    font-size: 12px;
    line-height: 20px;
    padding-inline-end: $crumb-padding;

    @include mobile {
        font-size: 14px;
    }

    .ChevronIcon {
        filter: invert(57%) sepia(0%) saturate(1%) hue-rotate(105deg) brightness(93%) contrast(90%);
        display: inline-block;
        margin-inline-start: $crumb-padding;
        width: 12px;
        height: 12px;
        inset-block-end: -2px;
    }

    &:last-child {
        .ChevronIcon {
            display: none;
        }

        :link {
            pointer-events: none;
        }
    }

    &::before {
        content: none;
    }

    &-Link {
        color: var(--breadcrumbs-color);
        display: inline-block;
        font-weight: 400;
        font-size: 12px;

        &:hover {
            color: var(--link-color);
        }

        &-Name {
            color: inherit;
        }
    }
}
