.NotificationList {
    position: fixed;
    z-index: 800;
    inset-block-start: calc(var(--header-height) + var(--breadcrumbs-height) + 20px);
    inset-inline-end: 30px;
    max-width: 550px;
    list-style: none;

    @include desktop {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-end;
    }

    @include mobile {
        overflow: hidden;
        max-width: 100vw;
        position: fixed;
        width: 100%;
        inset-block-start: var(--header-total-height);
        inset-inline-end: 0;
    }
}
